import moment from 'moment';

export const MonthView = () => {
  return (
    <div className="conatiner-fluid">
      <nav aria-label={moment().year()}>
        <ul className="pagination">
          {moment.months().map((month, index) => (
            <span key={month} id={index + 1} className="each-month">
              {month}{' '}
            </span>
          ))}
        </ul>
      </nav>
      <hr className="month-line"></hr>
      <table className="">
        <thead>
          <tr>
            {moment.weekdays().map(weekDay => (
              <th class="days-of-week">{weekDay}</th>
            ))}
          </tr>
        </thead>
        <tbody id="calendar-body"></tbody>
      </table>
    </div>
  );
};
